import React from 'react'

function PopUpTrans (props)  {



return (props.trigger) ? (
        <div className='mint_popup'>
            <div className='mint_popup_innner'>
            <button onClick={()=>{props.settrigger(false)}} className='mint_popup_close_btn' >X</button>
                {props.children}
            </div>
        </div>
        
        ) : "";
    
}

export default PopUpTrans