import banner from '../assets/png/nfta-banner.png'
import iconFooter from '../assets/png/nft-logo.png'
import mancha1 from "../assets/png/capa1.png"
import mancha2 from "../assets/png/capa2.png"
import MenuOpciones1 from './MenuOpciones1'
import { useSelector, useDispatch } from 'react-redux'
import { ethers } from 'ethers'
import { useState } from 'react'
import { useNavigate } from "react-router-dom";
//Popups
import PopUpLoading from './popUps/popUpLoading'
import PopUpTrans from './popUps/popTrans'
import PopUpMetamask from './popUps/popMetamask'
import PopCancelTrans from './popUps/popCancelTrans'
import PopNoMoney from './popUps/popNoMoney'
import PopNoGasMoney from './popUps/popNoGasMoney'
import BeatLoader from 'react-spinners/BeatLoader'



const menu =
  {
    eth: ['0.02 ETH','0.15 ETH', '0.2 ETH'],
    matic : [
      `150 MATIC`,
      `200 MATIC`,
    ]
  }

const Mint = ({id}) => {

  let dispatch = useDispatch()
  let navigate = useNavigate()
  const [popUpLoading, setpopUpLoading] = useState(false)
  const [transPopUp, setTransPopUp] = useState(false)
  const [metaPopUp, setMetaPopUp] = useState(false)
  const [popCancelTrans, setPopCancelTrans] = useState(false) 
  const [popNoMoney, setPopNoMoney] = useState(false)

  const blockchain = useSelector((store)=> store.blockchain)


  // Close Popup

  const closePopTrans = ()=>{
    setTimeout(()=>{
      setTransPopUp(false)
    }, 1000 * 5)
  }

  const closePopMeta = ()=>{
    setTimeout(()=>{
      setMetaPopUp(false)
    }, 1000 * 5)
  }

  const closepopCancelTrans = ()=>{
    setTimeout(()=>{
      setPopCancelTrans(false)
    }, 1000*5 )
  }

  const closepopNoMoney = () =>{
    setTimeout(()=>{
      setPopNoMoney(false)
    }, 1000*5)
  }

  // BLOCKCHAIN 

  const _spender = '0xDFCCA51eCEE2C481771D578B274cE9F7B264d3C6'
  const _amount = '1000000000000000000000000'


const allowance = async (_account)=>{
  const taxB = await blockchain.contratoTok.allowance(_account, _spender)
  .then((resp)=>{
    console.log('Respuesta allowance')
    const respuesta = Number(resp._hex).toLocaleString('fullwide', { useGrouping: false })
    
    if(respuesta < 30000000000000000000){
      const minteo = ()=>{
        const txC =  approve( 2, _account)
      }
      minteo()
  } else{
      minter( 2, _account)
      
  } 
  })
}

const approve = async ( _option, _account)=>{

  const txA = await blockchain. contratoTok.approve( _spender, _amount)
  await txA.wait().then((resp)=>{
    
      if(txA){
        setpopUpLoading(false)
        minter( 2, _account)
      }
    }
  ) 
}

// MINTER FUNCTION

const minter = async(_option,_account)=>{

  const tx1 = await blockchain.contracNFT.onlyWhitelisted()
  const tx2 = await blockchain.contracNFT.counter()
  const counter = parseInt(tx2, 18)
  
  try {

    if(tx1 == true && _option == 1){
      const options1 = {
          from: _account,
          value: ethers.utils.parseEther("0.0002")
        }
      
      const tx = await blockchain.contracNFT.mint(_option, 1 ,options1)
      setpopUpLoading(true)
      await tx.wait().then((resp)=>{
        if(tx){
          setpopUpLoading(false)
          setTransPopUp(true)
          closePopTrans()
          navigate("/profile")
        }
    }) 
    } else if(tx1 == false && _option == 1){
      
      const options2 = {
      from: _account,
      value: ethers.utils.parseEther("0.0015")
    }

      const tx = await blockchain.contracNFT.mint(_option, 1 ,options2)
      setpopUpLoading(true)
      await tx.wait().then((resp)=>{
        if(tx){
          setpopUpLoading(false)
          setTransPopUp(true)
          closePopTrans()
          navigate("/profile")
        }
    }) 

    } else if(counter >= 20 && _option == 1){
      const options3 = {
        from: _account,
        value: ethers.utils.parseEther("0.002")
      }

      const tx = await blockchain.contracNFT.mint(_option, 1 ,options3)
      setpopUpLoading(true)
      await tx.wait().then((resp)=>{
        if(tx){
          setpopUpLoading(false)
          setTransPopUp(true)
          closePopTrans()
          navigate("/profile")
        }
      }) 
    } else {
      
      const tx = await blockchain.contracNFT.mint(_option, 1)
      setpopUpLoading(true)
      await tx.wait().then((resp)=>{

        if(tx){
          setpopUpLoading(false)
          setTransPopUp(true)
          closePopTrans()
          navigate("/profile")
        }
      })
      
    }

  } catch (error) {
    console.log(error.code)
    if(error.code == 4001){
      setpopUpLoading(false)
      setPopCancelTrans(true)
      closepopCancelTrans()
    }else if(error.code == 'INSUFFICIENT_FUNDS'){
      setpopUpLoading(false)
      setPopNoMoney(true)
      closepopNoMoney()
    }else{ 
      setpopUpLoading(false)
    }
  }
}

  return (
    <div className='mint' id={id}>
      
      <PopUpLoading trigger={popUpLoading} settrigger={setpopUpLoading} >
              <div className='mint_div'>
                <div>
                    <h2 className='mint__titulo1'>Loading</h2>
                </div>
                <div className='mint_div_loader'>
                    <BeatLoader className='mint_popup_nft'/>
                </div>
              </div>
      </PopUpLoading>
      <PopUpTrans trigger={transPopUp} settrigger={setTransPopUp} >
              <div className='mint_div'>
                <div>
                    <h2 className='mint__titulo1'>Your NFT have been minted</h2>
                </div>
              </div>
      </PopUpTrans>
      <PopUpMetamask trigger={metaPopUp} settrigger={setMetaPopUp} >
              <div className='mint_div'>
                <div>
                    <h2 className='mint__titulo1'>Connect your Metamask</h2>
                </div>
              </div>
      </PopUpMetamask>
      <PopCancelTrans trigger={popCancelTrans} settrigger={setPopCancelTrans} >
              <div className='mint_div'>
                <div>
                    <h2 className='mint__titulo1'>Your Transfer Have been Cancel</h2>
                </div>
              </div>
      </PopCancelTrans>
      <PopNoMoney trigger={popNoMoney} settrigger={setPopNoMoney} >
              <div className='mint_div'>
                <div>
                    <h2 className='mint__titulo1'>You dont have enough moeny for the gas fees</h2>
                </div>
              </div>
      </PopNoMoney>
      
          <img src={mancha1} className="mancha1"alt="" />
          <img src={mancha2} className="mancha2"alt="" />
      <div className='mint__container'>
        <div className='mint__content'>

          <h1 className='titulo__mint'> PRESALE MINT PRICE 0.15 ETH </h1>
          <h2 className='sub__titulo1'>MINT PRICE 0.2 ETH</h2>
          <h3 className='sub__titulo'>(10,000 AVAILABLE)</h3>

          <p className='parrafo__mint'>10,000 Oubita NFTs created to bring financial freedom. Thanks to the rarity of each NFTs you
            will be able to get rewards in the Oubita Exchange and his metaverse space.
          </p>
          <p className='parrafo__mint'>Developed thought Polygon network blockchain and create to have governance at Oubita
            Exchange, Its personal an referable. Meet Oubita Exchange and Register to take advantage of your rewards
          </p>

          {/* <button className=' --boton__mint'>
            <a className='link__boton' target={'_blank'} href="https://www.oubita.com/#/">GO TO OUBITA.COM</a>
          </button> */}

          <div className="botones_mint">
            <div className='mint_div_buttons'>
              <p className="sub__titulo1">{menu.eth[1]}</p>
              <button className='boton --boton__mint --botonNFT'
              onClick={()=>{
                if(blockchain.account){
                  minter(1, blockchain.account)
                  setpopUpLoading(true)
                }else{
                  setMetaPopUp(true)
                  closePopMeta()
                }
              }}
              >MINT NFT</button>
            </div>
            <div className='mint_div_buttons'>
              <p className="sub__titulo1">{menu.matic[0]}</p>
              <button className='boton --boton__mint --botonNFT'
                onClick={()=>{
                  if(blockchain.account){
                    allowance(blockchain.account)
                    setpopUpLoading(true)
                  }else{
                    setMetaPopUp(true)
                    closePopMeta()
                  }
                }}
              >MINT NFT</button>
            </div>
            
          </div>

        </div>

        <div className='mint__img'>
          <img className='img' src={banner} alt="banner" />
        </div>
      </div>


      <div className='mint__footer'>
        <div className='footer__content'>

          <div className='footer__title'>
            <h1>LAUNCH OF 10,000 NFTS</h1>
          </div>

          <div className='--medio-border'></div>

          <div className='footer__copy'>
            <h4>PRESALE AT 0,15 ETH </h4>
            <h4>SET PRICE 0,2 ETH</h4>
            <h4>NETWORK: POLYGON</h4>
          </div>
        </div>

        <div className='footer__logo '>
          <img className='image' src={iconFooter} alt="" />
        </div>
      </div>
    </div>
  )
}

export default Mint
