import React from 'react'
import NftsBenefits from '../componets/NftsBenefits';
import PopUp  from './popUps/popUp';
import {useState} from 'react'

  const NftRender = ({data}) => {

  const [popUp, setpopUp] = useState(false)
  const [indexNft, setIndexNft] = useState(0) 

  const {NFTs} = data

 //Capture NFT 
  let theNft
  let nftImagen
  if(NFTs.length >= 1){
    theNft = NFTs[indexNft]
    const {image} = NFTs[indexNft]
    nftImagen  = image.slice(7)
  }else{console.log('NO HA LLEGADO LA DATA')}


  return (
    <div className='profile_nft'> 
    <PopUp trigger={popUp} settrigger={setpopUp} >
      <div >
        {theNft?(<img className='profile_popup_nft' src={`https://ipfs.io/ipfs/${nftImagen}`}/>):('')}
      </div>
    </PopUp>
      {NFTs.map((item, index)=>{
      
        let imagen = item.image.slice(7)
        let name = item.file.substring(0,item.file.length -4)
        return( 
          <div key={index} className='profile_nft_container'>
            <div className='profile_nft_cointainer_id_div'>
              <p >Oubita #{name}</p>
            </div>
            <div className='profile_nft_container_img_div profile_nft_container_imgSize:hover'>
              <button onClick={()=>{
                setpopUp(true)
                setIndexNft(index)
                }} className='profile_nft_container_button'>
    
                    <img  className='profile_nft_container_imgSize' src={`https://ipfs.io/ipfs/${imagen}`}/>
              </button>
            </div>
            <div className='profile_nft_container_name_div'>
          
            </div>
          </div>
        )
              })}

      
    </div>
  ) 
}

export default NftRender